<template>
  <div id="db" class="grid">
    <div class="grid__col--3">
      <h1>{{ currentMonthName }}</h1>

      <fieldset>
        <legend>Add an expense</legend>

        <label>
          Category:
          <input
            type="text"
            class="field"
            v-model="expenseCategory"
            @keyup.enter="recordExpense"
            ref="newExpenseCategory"
          />
        </label>

        <label>
          Amount
          <input
            type="number"
            class="field"
            v-model="expenseAmount"
            @keyup.enter="recordExpense"
            ref="newExpenseAmount"
          />
        </label>
      </fieldset>

      <fieldset>
        <legend>Income settings</legend>

        <label>
          Income each payday
          <input
            type="number"
            v-model="amountPaid"
          />
        </label>

        <label>
          First pay day
          <input
            type="date"
            v-model="formattedPayDay1"
          />
        </label>

        <label>
          Second pay day
          <input
            type="date"
            v-model="formattedPayDay2"
          />
        </label>
      </fieldset>
    </div>

    <div class="grid__col--9">
      <div class="table" ref="expenseTable">
        <div class="table__head">
          <div class="table__row">
            <div class="
                table__cell
                table__cell--head
                table__cell--numeric
              "
            >
              Amount
            </div>

            <div class="table__cell table__cell--head">
              Category
            </div>

            <div class="table__cell table__cell--head">
              Actions
            </div>
          </div>
        </div>

        <div class="table__body">
          <div class="table__row" v-for="(expense, i) in expenses">
            <div class="table__cell table__cell--numeric">
              ${{ expense.amount }}
            </div>

            <div class="table__cell">
              {{ expense.category }}
            </div>

            <div class="table__cell">
              <span
                class="action action--remove"
                @click="deleteExpense(i)"
              >
                &times;
              </span>

              <span
                class="action action--edit"
                @click="editExpense(i)"
              >
                edit
              </span>
            </div>
          </div>
        </div>

        <div class="table__foot">
          <div class="table__row">
            <div class="table__cell">
              <strong>Total</strong>
              ${{ amountSpent }}
            </div>

            <div class="table__cell">
              &nbsp;
            </div>

            <div class="table__cell">
              <strong>Remaining:</strong>
              {{ amountRemaining }}
            </div>
          </div>
        </div>
      </div>

      <p>
        <strong>Next pay day:</strong>
        {{ daysUntilNextPayDay }}
      </p>

      <p>
        <strong>You can spend:</strong>
        {{ amountPerDay }}
        per day
      </p>
    </div>
  </div>
</template>

<script>
import moment from 'moment';

export default {
  data () {
    return {
      currentMonthName: moment().format("MMMM"),
      editing: null,
      expenseAmount: "",
      expenseCategory: "",
      expenses: JSON.parse(localStorage.getItem("DB.expenses") || "[]"),
      payDay1: moment("2018-01-12"),
      payDay2: moment("2018-01-31"),
      amountPaid: parseFloat(localStorage.getItem("DB.amountPaid") || 0),
    };
  },

  watch: {
    amountPaid: function(value) {
      localStorage.setItem("DB.amountPaid", value);
    },

    expenses: function(currentExps, previousExps) {
      localStorage.setItem("DB.expenses", JSON.stringify(this.expenses));

      if (currentExps.length > previousExps.length) {
        var row = this.$refs
          .expenseTable
          .querySelector(".table__body .table__row:first-child");

        row.classList.add(
          "table__row--inserted",
          "table__row--highlighted"
        );

        setTimeout(function() {
          row.classList.remove("table__row--highlighted");

          setTimeout(function() {
            row.classList.remove("table__row--inserted");
          }, 1510);
        });
      }
    },
  },

  computed: {
    amountSpent: function() {
      return this.expenses.reduce(function(acc, exp) {
        return acc + parseFloat(exp.amount);
      }, 0).toFixed(2);
    },

    formattedPayDay1: {
      get: function() {
        return this.payDay1.format("YYYY-MM-DD");
      },

      set: function(value) {
        this.payDay1 = moment(value);
      },
    },

    formattedPayDay2: {
      get: function() {
        return this.payDay2.format("YYYY-MM-DD");
      },

      set: function(value) {
        this.payDay2 = moment(value);
      },
    },

    amountRemaining: function() {
      return "$" + this.getAmountRemaining();
    },

    amountPerDay: function() {
      return "$" + (
        this.getAmountRemaining() / this.getDaysRemaining()
      ).toFixed(2);
    },

    daysUntilNextPayDay: function() {
      if (moment().startOf("day") < this.payDay1.startOf("day")) {
        return this.payDay1.startOf("day").from(moment().startOf("day"));
      } else {
        return this.payDay2.startOf("day").from(moment().startOf("day"));
      }
    },
  },

  methods: {
    recordExpense: function(evt) {
      var amount = parseFloat(this.expenseAmount),
          category = this.expenseCategory,
          vm = this;

      var valid = validateFields(vm, amount, category);

      if (valid) {
        saveExpense(vm, amount, category);
        resetForm(vm);
      } else {
        return false;
      }

      function saveExpense(vm, amount, category) {
        var expenses = vm.expenses.slice(0);

        if (vm.editing) {
          expenses[vm.editing] = {
            amount: amount.toFixed(2),
            category: category,
          };
        } else {
          expenses.unshift({
            amount: amount.toFixed(2),
            category: category,
          });
        }

        vm.expenses = expenses;
      }

      function resetForm(vm) {
        vm.expenseAmount = "";
        vm.expenseCategory = "";
        vm.$refs.newExpenseCategory.focus();
      }

      function validateFields(vm, amount, category) {
        if (window.errorTimeout) {
          clearTimeout(window.errorTimeout);
          window.errorTimeout = null;
        }

        if ([0, NaN].includes(amount)) {
          vm.$refs.newExpenseAmount.classList.add("field--error");

          window.errorTimeout = setTimeout(function() {
            vm.$refs.newExpenseAmount.classList.remove("field--error");
          });

          vm.$refs.newExpenseAmount.focus();
          vm.$refs.newExpenseAmount.select();

          return false;
        } else if (!category.length) {
          vm.$refs.newExpenseCategory.classList.add("field--error");

          window.errorTimeout = setTimeout(function() {
            vm.$refs.newExpenseCategory.classList.remove("field--error");
          });

          vm.$refs.newExpenseCategory.focus();

          return false;
        } else {
          return true;
        }
      }
    },

    deleteExpense: function(i) {
      this.expenses.splice(i, 1);
    },

    editExpense: function(i) {
      var exp = this.expenses[i];
      this.editing = i;
      this.expenseAmount = exp.amount;
      this.expenseCategory = exp.category;
    },

    getDaysRemaining: function() {
      if (moment() < this.payDay1) {
        return this.payDay1.startOf("day")
          .diff(moment().startOf("day"), "days");
      } else {
        return this.payDay2.startOf("day")
          .diff(moment().startOf("day"), "days");
      }
    },

    getAmountRemaining: function() {
      return parseFloat(
        this.amountPaid - this.amountSpent
      ).toFixed(2);
    },
  },
}
</script>

<style lang="scss">
#db {
  font-family: Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 1rem;
  padding: 0;
}

fieldset {
  margin: 0 0 1rem;
  padding: 1rem;
}

label,
input {
  display: block;
}

input {
  font-size: 1.25rem;
  padding: 0.5rem 1rem;
}

label {
  margin: 0 0 1rem;
}

.field {
  transition: background-color 1.5s;
}

.field--error {
  background-color: Pink;
  transition: none;
}

.table {
  border: 1px solid #999;
}

.table__row {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #999;
  transition: background-color 0.2s;
}

.table__row--inserted {
  transition: background-color 1.5s;
}

.table__row--highlighted {
  background-color: LightSteelBlue;
  transition: none;
}

.table__body .table__row:nth-child(even) {
  background: rgba(0, 0, 0, 0.1);
}

.table__body .table__row:hover {
  background: rgba(0, 0, 0, 0.15);
}

.table__head {
  background: #999;
  box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
}

.table__body {
  height: 250px;
  overflow-y: auto;
  overflow-x: hidden;
}

.table__foot {
  background: #999;
  box-shadow: 0 -0.2rem 0.5rem rgba(0, 0, 0, 0.2);
}

.table__cell--head {
  font-weight: bold;
}

.table__cell {
  width: 50%;
  padding: 0.5rem 1rem;
}

.table__cell--numeric {
  text-align: right;
}

.table__cell--colspan-2 {
  width: 100%;
}

.table__foot {
  text-align: right;
}

.action {
  display: inline-block;
  padding: 0 0.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: color 0.2s;
}

.action::selection {
  background: none;
}

.action--edit {
  font-size: 0.9rem;
  font-weight: normal;
  color: blue;
}

.action--remove {
  color: red;
}

.action--remove:hover {
  color: darkred;
}

[class*="grid__col--"] {
  margin-right: 1rem;
}

@media (min-width: 625px) {
  .grid {
    display: flex;
    width: 100%;
  }

  .grid__col--3 {
    width: 25%;
  }

  .grid__col--6 {
    width: 50%;
  }

  .grid__col--9 {
    width: 75%;
  }

  [class*="grid__col--"]:last-child,
  [class*="grid__col--"]:only-child {
    margin-right: 0;
  }
}
</style>



